.navbar{
    display: flex;
    width:100%;
    height:max-content;
	position: absolute;
    gap:2rem;
	margin: 1rem;
	padding: 0;
	color: #f9f1e9;
	text-align: center;
	top: 4%;
	left: 50%;
	-webkit-transform: translate3d(-50%,-50%,0);
	transform: translate3d(-50%,-50%,0);
}

.navbar p{
    cursor: pointer;
	font-size: 1.2em;
	letter-spacing: 0.1em;
    padding: 1rem;
}
.navbar p:hover{
	text-transform: uppercase;
    cursor: pointer;
    color:yellow;
	font-size: 1.2em;
	letter-spacing: 0.1em;
    padding: 1rem;
}


@media only screen and (max-width : 768px) {
	.navbar{
        flex-direction: column;
        top: 13%;
		left:46%;
        gap: 0rem;
	}
    .navbar p{
		padding: 0.01rem 0 0.01rem 0;
		margin:0.5rem 1rem 0.5rem 1rem;
        font-size: 1.4rem;
	}
}